
<template>
  <div class="Add_manage">
      <van-radio-group v-model="radio">
      <div class="addressItem" v-for="(a,index) in addressItem" :key="a.id">
        <div class="ItemInfo">
          <div>收货人：{{a.real_name}} {{a.phone}}</div>
          <div>收货地址:{{a.province}}{{a.city}}{{a.district}}{{a.detail}}</div>
        </div>
        <div class="ItemSet"><van-radio :name="index" checked-color="#ee0a24">设置默认</van-radio></div>
      </div>
      </van-radio-group>
  </div>
</template>

<script>
export default {
  name: "Add_manage",

  data() {
    return {
      addressItem: [],
       radio: 0
    };
  },

  components: {},
  methods: {},
  created() {
    this.$axios
      .get("http://47.115.51.185/api/address/list?page=1&limit=20")
      .then((data) => {
        if (data.data.status == 200) {
          this.addressItem = data.data.data;
          console.log(this.addressItem);
        } else {
          this.$router.push("/login");
        }
      });
  },
  computed: {},
};
</script>

